import {sliderPortfolio} from './modules/sliders.js'
import {modalBig, modalDirector} from './services/constants.js'
import observer from './services/observer.js'
import write from './modules/write.js'

import {showCloseFilter, itemsFilter} from './modules/filter.js'

import {handleScroll, hamburgerMenuClick} from './modules/menu.js'

document.addEventListener('DOMContentLoaded', () => {
    sliderPortfolio()

    window.addEventListener('scroll', () => handleScroll())

    observer()

    if (window.innerWidth < 992) {
        onAction('touchend')
    } else {
        onAction('click')
    }

    function onAction(action) {
        window.addEventListener(action, e => {
            write(e)
            if (e.target.closest('.portfolio__accordion-item')) {
                const accordionItem = e.target.closest(
                    '.portfolio__accordion-item'
                )
                const accordionBlock = accordionItem.querySelector(
                    '.portfolio__accordion-block'
                )
                accordionBlock.classList.toggle('height')
            } else if (
                e.target.closest('.menu__list') ||
                e.target.closest('.hamburger')
            ) {
                hamburgerMenuClick()
            } else if (e.target.classList.contains('menu__center-contact')) {
                modalOpen(modalBig, 'overlay-modal__active', 'modal__active')
            } else if (
                e.target.classList.contains('modal__close') ||
                e.target.classList.contains('overlay-modal')
            ) {
                modalClose('overlay-modal__active', 'modal__active')
            } else if (e.target.classList.contains('footer__left-write')) {
                modalOpen(
                    modalDirector,
                    'overlay-modal__active',
                    'modal__active'
                )
            } else if (
                e.target.closest('.portfolio__center') ||
                e.target.closest('.portfolio__filter-close')
            ) {
                showCloseFilter()
            }
        })
    }

    itemsFilter()
})
